<script lang="ts" setup>
/**
 * Componente "n-date-picker" com opção de v-model:date="campoTipoData"
 */
import _ from "lodash";
import { NDatePicker } from "naive-ui";
import { computed } from "vue";

const props = withDefaults(
  defineProps<{
    date: Date | null;
    mini?: boolean;
  }>(),
  {
    date: () => null,
    mini: () => false,
  }
);

const emit = defineEmits<{
  (e: "update:date", value: Date | null): void;
}>();

const dateNumber = computed({
  get() {
    if (_.isDate(props.date)) {
      return props.date.getTime();
    }
    return null;
  },
  set(v: number | null) {
    emit("update:date", v === null ? null : new Date(v));
  },
});
</script>

<template>
  <n-date-picker
    v-model:value="dateNumber"
    type="date"
    clearable
    placeholder=""
    :class="{ mini: props.mini }"
    class="date-box">
  </n-date-picker>
</template>
