<script lang="ts" setup>
import { computed, provide, ref, type Ref, useSlots } from "vue";
import { StopwatchOutline } from "@vicons/ionicons5";
import Icon from "../display/Icon.vue";

const props = defineProps<{
  model?: Ref<any>;
  labelWidth?: number | string;
  loading?: boolean;
}>();

type ErrorFormItem = {
  field: string;
  message: string;
};

const root = ref<HTMLElement | null>(null);
const slots = useSlots();
const showErrors = ref(false);

root.value?.addEventListener("submit", (_) => false);

const errors = computed(function () {
  if (showErrors.value && props.model) {
    const plain = JSON.parse(JSON.stringify(props.model));
    return validateDataset(plain);
  } else {
    return new Array<ErrorFormItem>();
  }
});

provide("errors", errors);
provide("labelWidth", props.labelWidth);

async function validate(): Promise<Array<ErrorFormItem>> {
  if (!props.model)
    alert("Formulário sem declaração de modelo.\n\n<Form :model=''></Form>");

  showErrors.value = true;

  const plain = JSON.parse(JSON.stringify(props.model));

  return validateDataset(plain);
}

// limpa a lista de errors e re-calcula todas as linhas
function validateDataset(data: any) {
  const errorList = new Array();
  const items = slots.default ? slots.default() : [];
  validateListItem(errorList, data, items);
  return errorList;
}

function validateListItem(errorList: any, data: any, items: any) {
  for (let i = 0; i < items.length; i++) {
    const item = items[i];

    if (!item.props?.field && !item.props?.required && !item.props?.validate) {
      if (item.children?.length > 0) {
        validateListItem(errorList, data, item.children);
      } else {
        continue;
      }
    }

    if (!item.props) continue;

    const { field, required, validate: validate2 } = item.props;
    const value = !field ? data : resolvePath(data, field);
    const isEmpty =
      value === null ||
      value === undefined ||
      new String(value).trim().length === 0;

    if (isEmpty && required !== false && required !== void 0) {
      errorList.push({
        field,
        message: "Campo obrigatório",
      });
    }
    if (validate2 && !isEmpty) {
      const result = validate2(value);
      if (result !== true) {
        errorList.push({
          field,
          message: result,
        });
      }
    }
  }
}

// remove todas as mensagens
function validateReset() {
  errors.value.length = 0;
  showErrors.value = false;
}

function resolvePath(obj: any, field: string, defaultValue?: any) {
  if (field === "*") return obj;
  return field
    .split(/[\.\[\]\'\"]/)
    .filter((p) => p)
    .reduce((o, p) => (o ? o[p] : defaultValue), obj);
}

defineExpose({ validate, validateReset });
</script>

<template>
  <form class="form n-block" ref="root" action="#" method="GET">
    <p v-if="loading" class="form-loading">
      <Icon :name="StopwatchOutline" class="form-loading-icon" />
      Aguarde...
    </p>
    <div class="form-content" :class="{ 'form-loading-content': loading }">
      <slot></slot>
    </div>
  </form>
</template>
