<script setup lang="ts">
import { CloudUploadSharp } from "@vicons/ionicons5";

const props = defineProps<{
  multiple?: boolean;
}>();

const emit = defineEmits<{
  (e: "file-select", files: any[]): void;
}>();

async function fileChange(event: any) {
  const files = event.target.files;
  const result = [];

  for (let i = 0; i < files.length; i++) {
    const file = files[i];

    const item = {
      filename: file.name,
      contentBase64: await readFile(file),
    };
    result.push(item);
  }

  emit("file-select", result);
}

// Lê o arquivo enviado pelo input type="file" e converte para base64
async function readFile(file: any): Promise<string> {
  // retorna uma promise com resultado do conteudo lido
  return new Promise(function (resolve, reject) {
    const reader = new FileReader();

    reader.onloadend = () => {
      const content = reader.result as string;
      const base64String = content.substring(content.indexOf(",") + 1);
      resolve(base64String);
    };

    reader.onerror = (e) => {
      reject(e);
    };

    reader.readAsDataURL(file);
  });
}
</script>

<template>
  <Btn color="primary" :icon="CloudUploadSharp">
    <input
      class="btn-file-upload"
      type="file"
      @change="fileChange"
      :multiple="multiple" />
    <slot>Upload</slot>
  </Btn>
</template>
