<script lang="ts" setup>
import { NAutoComplete } from "naive-ui";
import { ref, watch } from "vue";
/**
 * Componente "n-auto-complete" que garante o "null" no "v-model" quando componente está vazio
 * <SearchBox v-model="searchModel" :search-api-fn="conceitoService.search"></SearchBox>
 */

enum State {
  Initial = "Initial",
  Typing = "Typing",
  Selected = "Selected",
  Clear = "Clear",
}

const props = withDefaults(
  defineProps<{
    modelValue?: any;
    searchApiFn: (input: any) => Promise<any[]>;
    valueKey: string | number;
    textKey: string | number;
    delay?: number;
  }>(),
  {
    delay: () => 0.4,
  }
);

const emit = defineEmits<{
  (e: "update:modelValue", args: any | null): void;
}>();

const state = ref<State>(State.Initial);

const localValue = ref<any>(null);
const localText = ref<string | null>(null);
const innerText = ref<string | null>(null);

const options = ref<{ value: any; label: string }[]>([]);

const loading = ref<boolean>(false);
const timer = ref<number>();

watch(() => props.modelValue, onModelChange);
watch(() => innerText.value, onInput);

//observa quando model alterado por fora
async function onModelChange(newValue: any) {
  if (newValue === localValue.value) return;

  const v = getValue(newValue);
  if (v == null) {
    clear();
  } else {
    try {
      loading.value = true;

      const data = {
        [props.valueKey]: v,
        [props.textKey]: null,
      };

      const result = await props.searchApiFn(data);

      if (result.length > 0) {
        state.value = State.Selected;
        localValue.value = v;
        localText.value = result[0][props.textKey];
        innerText.value = result[0][props.textKey];
      } else {
        clear();
      }
    } finally {
      loading.value = false;
    }
  }
}

async function onInput(newText: string | null) {
  if (state.value === State.Typing || state.value === State.Clear) {
    if (getText(newText) === null) {
      clear();
    } else {
      await handleSearch(newText);
    }
  }
}

function onSelect(newValue: any) {
  state.value = State.Selected;

  const v = getValue(newValue);
  localValue.value = v;
  localText.value = options.value.find((x) => x.value === newValue).label;

  emit("update:modelValue", v);
}

function onFocus() {
  state.value = State.Typing;
}

//limpa componente caso mude input
function onBlur() {
  if (state.value === State.Typing) {
    if (localValue.value === null) {
      clear();
    } else {
      if (innerText.value !== localText.value) {
        clear();
      } else {
        state.value = State.Selected;
      }
    }
  }
}

async function handleSearch(newText: string | null) {
  if (props.delay > 0) {
    clearTimeout(timer.value);

    timer.value = setTimeout(async () => {
      await handleSearchRequest(newText);
    }, props.delay * 1000);
  } else {
    await handleSearchRequest(newText);
  }
}

async function handleSearchRequest(newText: string | null) {
  try {
    loading.value = true;

    const data = {
      [props.valueKey]: null,
      [props.textKey]: newText,
    };

    const opts = await props.searchApiFn(data);

    options.value = opts.map((x) => ({
      value: x[props.valueKey],
      label: x[props.textKey],
    }));
  } finally {
    loading.value = false;
  }
}

function clear() {
  state.value = State.Clear;
  localValue.value = null;
  localText.value = null;
  innerText.value = null;

  if (props.modelValue !== null) emit("update:modelValue", null);
}

function getValue(value: any) {
  if (value === null || value === undefined || value === "") {
    return null;
  }
  return value;
}

function getText(value: string | null) {
  if (
    value === null ||
    value === undefined ||
    value === "" ||
    value.toString().trim().length === 0
  ) {
    return null;
  }
  return value;
}
</script>

<template>
  <n-auto-complete
    v-model:value="innerText"
    :options="options"
    @focus="onFocus"
    @blur="onBlur"
    :onSelect="onSelect"
    placeholder=""
    :loading="loading"
    clearable
    blur-after-select>
  </n-auto-complete>
</template>
