<script lang="ts" setup>
import { computed, ref, type Component } from "vue";
import Icon from "../display/Icon.vue";
import { Reload } from "@vicons/ionicons5";

const props = withDefaults(
  defineProps<{
    color?: "default" | "primary" | "success" | "warning" | "info" | "error";
    mode?: "default" | "link";
    full?: boolean;
    icon?: Component;
    disabled?: boolean;
    toggle?: boolean;
    value: boolean;
    loading?: boolean;
    mini?: boolean;
    to?: string;
  }>(),
  {
    color: () => "default",
    mode: () => "default",
    full: () => false,
    disabled: () => false,
    toggle: () => false,
    value: () => false,
    mini: () => false,
    loading: () => false,
  }
);

const emit = defineEmits<{
  (e: "click", event: any): void;
  (e: "update:value", event: any): void;
}>();

const value = ref<boolean>(props.value);

const classNames = computed(() => [
  "btn",
  {
    "btn-button": props.mode === "default",
    "btn-link": props.mode === "link",
    "btn-is-disabled": props.disabled || props.loading,
    "btn-is-full": props.full,
    "btn-is-loading": props.loading,
    "btn-is-mini": props.mini,
    "btn-pressed": props.value,
  },
  "btn-" + props.color,
]);

const componentName = computed(() =>
  props.disabled || props.loading ? "span" : props.to ? "RouterLink" : "button"
);

function onClick(event: any) {
  if (!props.disabled) {
    emit("click", event);

    if (props.toggle) {
      value.value = !value.value;
      emit("update:value", value.value);
    }
  }
}
</script>

<template>
  <component
    :is="componentName"
    :class="classNames"
    :to="to"
    type="button"
    @click="onClick">
    <Icon v-if="icon && !loading" :name="icon" />
    <Icon v-if="icon && loading" :name="Reload" :spin="true" />
    <span v-if="loading && $slots.default" class="btn-text">Aguarde...</span>
    <span v-if="!loading" class="btn-text">
      <slot></slot>
    </span>
  </component>
</template>
