<script lang="ts" setup>
import { computed, ref, type Component } from "vue";
import Icon from "./Icon.vue";
import { ChevronDown, ChevronUp, TrashBinOutline } from "@vicons/ionicons5";
import Btn from "../buttons/Btn.vue";

const props = defineProps<{
  title?: string;
  icon?: Component;
  modelValue: boolean;
  showReset: boolean;
}>();

const emit = defineEmits<{
  (e: "update:modelValue", args: any): void;
  (e: "reset", args: any): void;
}>();

const collapsed = ref(!!props.modelValue);
const collapsedIcon = computed(() =>
  collapsed.value ? ChevronDown : ChevronUp
);

function onClickCollapse() {
  collapsed.value = !collapsed.value;
  emit("update:modelValue", collapsed.value);
}

function onResetClick() {
  emit("reset", null);
}
</script>

<template>
  <div class="card n-block">
    <div class="card-header" v-if="title">
      <div class="card-header-left">
        <Icon v-if="icon" :name="icon" />
        <div class="card-title">{{ title }}</div>
        <slot name="header-left"></slot>
      </div>
      <div class="card-header-right">
        <Btn
          v-show="showReset"
          :icon="TrashBinOutline"
          :mini="true"
          style="margin-right: 1rem"
          @click="onResetClick">
          Limpar
        </Btn>
        <icon :name="collapsedIcon" @click="onClickCollapse" />
      </div>
    </div>
    <div class="card-content" v-show="!collapsed">
      <slot></slot>
    </div>
  </div>
</template>
