<script setup lang="ts">
import Icon from "../display/Icon.vue";
import { computed, ref, inject, type Component } from "vue";

const props = withDefaults(
  defineProps<{
    index: number;
    title: string;
    icon: Component;
    disabled?: boolean;
    visible: boolean;
  }>(),
  {
    visible: () => true,
  }
);

const selectedIndex = inject("selectedIndex", ref(0));

const isSelectedTab = computed(() => selectedIndex.value === props.index);

function selectTab() {
  if (!props.disabled) {
    selectedIndex.value = props.index;
  }
}
</script>

<template>
  <label
    class="tabs-label"
    @click="selectTab"
    :class="{
      'tab-disabled': disabled,
      'tab-selected': isSelectedTab,
      'tabs-hide': !visible,
    }">
    <Icon :name="icon" v-if="icon" class="tabs-icon" />
    {{ title }}
  </label>
  <section class="tabs-content" :class="{ 'tabs-hide': !visible }">
    <slot></slot>
  </section>
</template>
