<script lang="ts" setup>
import InputBox from "./InputBox.vue";
import { onMounted, ref, watch } from "vue";
import * as mask from "../../../core/mask";

const props = withDefaults(
  defineProps<{
    value: string;
    mask: string;
    mini?: boolean;
  }>(),
  {
    value: () => "",
    mask: () => "",
    mini: () => false,
  }
);

const emit = defineEmits<{
  (e: "update:value", value: string | null): void;
}>();

const textValue = ref<string | null>(mask.apply(props.value, props.mask));
const lastValue = ref<string | null>(null);

onMounted(() => onUpdate(props.value));

watch(
  () => props.value,
  (newValue) => {
    if (newValue === null && lastValue.value !== null) {
      textValue.value = lastValue.value = null;
    }
    if (newValue === null) return;
    const masked = mask.apply(newValue, props.mask);
    textValue.value = masked;
  }
);

function onUpdate(value: string) {
  const masked = mask.apply(value, props.mask);

  if (masked !== lastValue.value) {
    emit("update:value", mask.remove(masked));
  }

  lastValue.value = masked;
}

function onBlur() {
  const masked = mask.apply(textValue.value, props.mask);
  textValue.value = masked;
}
</script>

<template>
  <InputBox
    v-model:value="textValue"
    class="memo-box"
    :class="{ mini: props.mini }"
    v-model:maxlength="props.mask.length"
    @update:value="onUpdate"
    @blur="onBlur" />
</template>
