import {
  Checkmark,
  Close,
  EllipseOutline,
  OpenOutline,
} from "@vicons/ionicons5";
import type {
  DataTableColumn,
  DataTableRowKey,
  DataTableSortState,
} from "naive-ui";
import { computed, h, reactive, ref, watch, type Ref } from "vue";
import { RouterLink, useRoute } from "vue-router";
import { exportToExcel } from "../../../core/clipboard";
import { notification } from "../../../core/notification";
import { useCulture } from "../../../core/culture";
import * as mask from "../../../core/mask";
import * as objectPath from "object-path";
import * as _ from "../../../core/underscore";
import Btn from "../buttons/Btn.vue";
import InputBox from "../inputs/InputBox.vue";
import MonthBox from "../inputs/MoneyBox.vue";
import DecimalBox from "../inputs/DecimalBox.vue";
import ComboBox from "../inputs/ComboBox.vue";
import NumberBox from "../inputs/NumberBox.vue";
import MoneyBox from "../inputs/MoneyBox.vue";
import CheckBox from "../inputs/CheckBox.vue";
import DateBox from "../inputs/DateBox.vue";
import Icon from "../display/Icon.vue";
import profile from "../../../core/profile";

const culture = useCulture();
//const cultureEN = useCulture("en-us");

const REM = 20; // fator de conversão de with (em rem) para px

type FilterInfo = {
  component: string;
  options: any;
  dataType:
    | "string"
    | "number"
    | "money"
    | "decimal"
    | "date"
    | "month"
    | "boolean";
  ops?: { value: string; label: string }[];
  op?: string;
  value?: [];
};

type GridViewItemOptions<T> = {
  align?: "left" | "center" | "right";
  color?: "default" | "primary" | "info" | "success" | "warning" | "error";
  mode?: "default" | "link";
  icon?: any;
  mini?: boolean;
  width?: number;
  title?: string | ((row: T) => string);
  disabled?: (row: T) => boolean;
  format?: string;
  sort?: string; // indique o campo a ser ordenado (no caso de "render")
  visible?: boolean;
  filter?: boolean | string;
};

const filterOps = [
  ["=", "="],
  [">", ">"],
  [">=", ">="],
  ["<", "<"],
  ["<=", "<="],
  ["Diferente", "!="],
  ["Entre", "between"],
  ["Iniciado por", "startsWith"],
  ["Contem", "contains"],
];

const filterControls = {
  InputBox: ["value", () => InputBox],
  MoneyBox: ["value", () => MoneyBox],
  DateBox: ["date", () => DateBox],
  MonthBox: ["date", () => MonthBox],
  DecimalBox: ["value", () => DecimalBox],
  NumberBox: ["value", () => NumberBox],
  ComboBox: ["value", () => ComboBox],
};

export class GridViewOptions<T> {
  private _key: string;
  private _columns: Ref<any[]>; // DataTableColumn[]
  private _data: Ref<T[]>;
  private _selectedItems: Ref<DataTableRowKey[]>;
  private _options: any;
  private _profileID: string | null;
  private _sortColumnKey: Ref<string | null>;
  private _sortOrder: Ref<"descend" | "ascend" | null>;

  /**
   * Retorna o objeto de opções utilizado no NDataTable
   */
  get options() {
    return this._options;
  }

  /**
   * Retorna a lista de todas os IDs que estão selecionados. Lista vazia em caso de nenhum
   */
  get selectedItems() {
    return this._selectedItems.value;
  }

  /**
   * Retorna o ID do item selecionado (null em caso de nenhum)
   */
  get selectedItem(): DataTableRowKey | null {
    return this._selectedItems.value.length == 0
      ? null
      : this._selectedItems.value[0];
  }

  /**
   * Adiciona um ID na lista dos selecionados e marca na GridView
   */
  set selectedItem(value: DataTableRowKey | null) {
    this._selectedItems.value.length = 0;
    if (value != null) {
      this._selectedItems.value.push(value);
    }
  }

  /**
   * Cria uma nova instancia de GridViewExOptions para ser utilizada no componente grid-view
   * @param key A chave primaria ou identificador unico da Dto
   */
  constructor(key: string) {
    this._key = key;
    this._columns = ref([]);
    this._data = ref([]);
    this._selectedItems = ref<DataTableRowKey[]>([]);
    this._options = this.createOptions();
    this._profileID = null;
    this._sortOrder = ref(null);
    this._sortColumnKey = ref(null);
  }

  /* private functions */
  private getFilterLabel() {
    const filters = this.getFilters();
    const text = [];

    for (let i = 0; i < filters.length; i++) {
      const { field, op, value } = filters[i];
      const title = this._columns.value.find((x) => x.key === field)?.title;
      const opText = filterOps.find((x) => x[1] === op)[0].toLowerCase();
      const valueText =
        value.length === 1
          ? Array.isArray(value[0])
            ? value[0].join(", ")
            : value[0]
          : value[0] + '" a "' + value[1];

      text.push(title + " " + opText + ' "' + valueText + '"');
    }

    return text.join(" e ");
  }

  // retorna uma lista de filtros aplicaveis
  private getFilters() {
    const filters = this._columns.value
      .filter((x) => x.filterInfo)
      .filter(
        (x) => !_.isEmpty(x.filterInfo.op) || x.filterInfo.ops.length === 0
      )
      .filter((x) => !_.isEmpty(x.filterInfo.value[0]))
      .filter(
        (x) =>
          !(
            Array.isArray(x.filterInfo.value[0]) &&
            x.filterInfo.value[0].length === 0
          )
      )
      .filter(
        (x) =>
          !(x.filterInfo.op === "between" && _.isEmpty(x.filterInfo.value[1]))
      )
      .map((x) => ({
        field: x.key,
        op: x.filterInfo.op || "=",
        value: x.filterInfo.value,
      }));

    return filters;
  }

  // aplica o filtro sobre o resultado original e retorna uma lista com os registros filtrados
  private applyFilter() {
    //
    const result: [] = [];
    const filters = this.getFilters();

    if (filters.length === 0) return this._data.value;

    for (let i = 0; i < this._data.value.length; i++) {
      //
      const row = this._data.value[i];
      let add = false;

      for (let j = 0; j < filters.length; j++) {
        //
        const filter = filters[j];

        const rowValue = objectPath.get(row, filter.field);
        const op = filter.op;

        const values = Array.isArray(filter.value[0])
          ? filter.value[0]
          : [filter.value[0]];
        const value2 = filter.value[1];

        add = false;

        // implementa um OU entre os valores
        for (let z = 0; z < values.length; z++) {
          // pega o valor informado pelo usuario
          const value = values[z];

          // verifica startsWith apenas para string
          if (
            op === "startsWith" &&
            rowValue.toUpperCase().startsWith(value.toUpperCase())
          ) {
            add = true;
          }
          // verifica contains apenas para substring
          else if (
            op === "contains" &&
            rowValue.toUpperCase().indexOf(value.toUpperCase()) >= 0
          ) {
            add = true;
          }
          // verifica demais operações
          else {
            // pega o resultado da comparação
            const diff = _.compare(rowValue, value);

            if (
              (op === "=" && diff === 0) ||
              (op === ">" && diff > 1) ||
              (op === ">=" && diff >= 0) ||
              (op === "<" && diff < 0) ||
              (op === "<=" && diff <= 0) ||
              (op === "!=" && diff !== 0)
            ) {
              add = true;
            }
            // verifica caso between
            else if (op === "between" && diff >= 0) {
              // compara com o segundo resultado
              const diff2 = _.compare(rowValue, value2);
              if (diff2 <= 0) add = true;
            }
          }
          // interrompe loop de valores no primeiro valor verdadeiro
          if (add) break;
        }
        // interrompe no primeiro filtro que retornar negativo
        if (!add) break;
      }

      if (add) result.push(row);
    }

    return result;
  }

  // converte todos os dados filtrados em registros pra exportação pra excel
  private getExcelData() {
    const result = [];

    for (let i = 0; i < this._data.value.length; i++) {
      const row = this._data.value[i];
      const columns = {};

      for (let j = 0; j < this._columns.value.length; j++) {
        const column = this._columns.value[j];

        if (!column.visible) continue;

        if (column.visible === true) {
          let value = row[column.key];
          const format = column.filterInfo.format;

          if (format) {
            value = format(value);
          }

          columns[column.title] = value;
        }
      }

      result.push(columns);
    }

    return result;
  }

  /**
   * Carrega a gridview utilizando uma função assincrona
   * @param asyncLoad Metodo assincrono que retorna um array do tipo T da gridview
   */
  async load(asyncLoad: () => Promise<T[]>) {
    this._options.loading = true;

    try {
      const values = await asyncLoad();
      this._data.value = values;
    } finally {
      this._options.loading = false;
    }
  }

  /**
   * Indica que a GridView terá seleção multipla (checkbox)
   */
  multiple(options?: { disabled?: (row: T) => boolean }): GridViewOptions<T> {
    this._columns.value.push({
      type: "selection",
      multiple: true,
      width: 2 * REM,
      disabled: options?.disabled,
    });
    return this;
  }

  /**
   * Indica que a GridView terá seleção simples (radio)
   */
  select(options?: { disabled?: (row: T) => boolean }): GridViewOptions<T> {
    this._columns.value.push({
      type: "selection",
      multiple: false,
      width: 2 * REM,
      disabled: options?.disabled,
    });
    return this;
  }

  /**
   * Adiciona suporte a scroll horizontal na tabela.
   * As colunas definidas antes destes metodos serão fixadas a esquerda
   * A largura total da tabela é computada pela somas dos width de cada coluna. Quando uma GridView utiliza
   * este metodo, todas as colunas de tamanho variavel devem estar preenchidas para o scroll funcionar corretamente
   */
  freeze() {
    // fixa todas as colunas anteriores a esquerda
    for (let i = 0; i < this._columns.length; i++) {
      this._columns[i].fixed = "left";
      if (!this._columns[i].width)
        alert(
          "O metodo GridViewExOptions.freeze() encontrou colunas sem definição de tamanho antes de chamar o metodo"
        );
    }

    return this;
  }

  useProfile(profileID?: string) {
    profile.get(this._profileID).then((profileValue) => {
      if (profileValue) {
        const data = JSON.parse(profileValue);
        this._columns.value = sortColumns(this._columns.value, data.columns);
        this._options.pagination.pageSize = data.pageSize || 10;
        this._sortColumnKey.value = data.sortColumnKey || null;
        this._sortOrder.value = data.sortOrder || null;
        if (data.sortColumnKey) {
          const column = this._columns.value.find(
            (x) => x.key == data.sortColumnKey
          );
          if (column) {
            column.sortOrder = data.sortOrder;
          }
        }
      }

      // esta ultima coluna ajusta a largura das colunas quando houver menos colunas do que largura
      const lastColumn: DataTableColumn = {
        title: " ",
        key: "_last",
        width: undefined, // deve ser undefined pra indicar que ela cresce
        render: (row) => " ",
      };

      this._columns.value.push(lastColumn);

      // utiliza o profile por parametro ou gerado automatico
      this._profileID = profileID || getProfileID(this._key);
    });

    return this;
  }

  /**
   * Adiciona uma coluna do tipo botão na gridview
   * @param text Texto a exibir no botão
   * @param onclick Evento do click do botão
   * @param options Opções de renderização (align, disabled, width, color, mode, title, icon)
   */
  button(
    text: string,
    onclick: (row: T) => void,
    options?: GridViewItemOptions<T>
  ): GridViewOptions<T> {
    const column: DataTableColumn = {
      key: text,
      title: " ",
      align: options?.align,
      disabled: options?.disabled,
      width: getWidth(options?.width, 3),
      render(row: any) {
        return h(
          Btn,
          {
            color: options?.color || "default",
            mode: options?.mode || "default",
            mini: options?.mini,
            title: options?.title,
            icon: options?.icon,
            onClick: () => onclick(row),
          },
          { default: () => text }
        );
      },
    };

    this._columns.value.push(column);

    return this;
  }

  /**
   * Adiciona uma coluna do tipo botão com um icone na GridView
   * @param name Referencia do icone. Utilizar import { NomeIcone } from "@vicons/ionicons5"
   * @param onclick Metodo para capturar o evento de click neste icone
   * @param options Opções de renderização (width, align, title, color, disabled)
   */
  icon(
    icon: any,
    onclick: (row: T) => void,
    options?: GridViewItemOptions<T>
  ): GridViewOptions<T> {
    const column: DataTableColumn = {
      key: icon.name,
      title: " ",
      align: options?.align || "center",
      width: getWidth(options?.width, 2),
      render(row: any) {
        const disabled = options?.disabled && options?.disabled(row);
        return h(Icon, {
          name: icon,
          title: options?.title,
          style: {
            //--default-font, primary-color
            color: `var(--${
              disabled
                ? "disabled-color"
                : options?.color
                ? options.color + "-color"
                : "default-font"
            })`,
            cursor: disabled ? "not-allowed" : "pointer",
          },
          onclick: () => {
            if (disabled) return;
            onclick(row);
          },
        });
      },
    };

    this._columns.value.push(column);

    return this;
  }

  /**
   * Adiciona uma coluna a GridView do tipo edição de registro. Pode renderizar apenas um icone (quando o parametro onClickOrUrl for uma função)
   * ou renderizar um link com um icone apontando para o template de navegacao.
   * Um template de navegação é representado por ex "/pat/processo/{idProcesso}" aonde idProcesso é um field do objeto row T
   * @param onClickOrUrl Quando um metodo, dispara no click do icone passando a row T. Se uma string, é um template de navegação
   * @param options Opções de renderização ()
   */
  edit(
    onClickOrUrl: ((row: T) => void) | string,
    options?: GridViewItemOptions<T>
  ) {
    // define opções padrões
    const iconOptions = {
      title: "Editar registro",
      color: "primary",
    };
    Object.assign(iconOptions, options);

    // caso seja um metodo, apenas renderiza um icone
    if (typeof onClickOrUrl === "function")
      return this.icon(OpenOutline, onClickOrUrl, iconOptions);

    // caso seja string, gera um novo RouterLink
    const column: DataTableColumn = {
      key: "edit",
      title: " ",
      align: options?.align || "center",
      width: getWidth(options?.width, 2),
      render(row: any) {
        const disabled = options?.disabled && options.disabled(row);
        const icone = () =>
          h(Icon, {
            name: OpenOutline,
            title: options?.title || "Editar registro",
            style: {
              //--default-font, primary-color
              color: disabled
                ? "var(--disabled-color)"
                : `var(--${
                    options?.color ? options.color + "-color" : "primary-color"
                  })`,
              cursor: disabled ? "not-allowed" : "pointer",
            },
          });

        if (disabled) {
          return h("span", null, icone());
        }

        return h(
          RouterLink,
          {
            to: onClickOrUrl.replace(/(\{[\w\\.]+\})/g, (m: string) => {
              const field = m.substring(1, m.length - 1);
              return objectPath.get(row, field);
            }),
          },
          icone
        );
      },
    };

    this._columns.value.push(column);

    return this;
  }

  /**
   * Adiciona uma coluna na gridview com os dados vinculadas ao field do dataSource
   * @param header Titulo na header
   * @param key Chave/Caminho para obter o dado do objeto
   * @param width Largura da coluna (em 'rem'). Sempre deixar uma das colunas sem este parametro (= null) para o responsivo
   * @param options Opções de renderização (align, disabled, format)
   */
  column(
    header: string,
    field: string,
    width?: number,
    options?: GridViewItemOptions<T>
  ): GridViewOptions<T> {
    const column: DataTableColumn = {
      title: header,
      key: field,
      width: getWidth(width, undefined),
      align: options?.align,
      visible: _.isEmpty(options?.visible) ? true : !!options?.visible,
      disabled: options?.disabled,
      sorter: (row1, row2) => customSort(field, row1, row2),
    };

    if (options?.format) {
      column.render = (row: any) => {
        const value = objectPath.get(row, field);
        // se o valor a exibir for uma string, formata como mascara
        if (typeof value === "string") return mask.apply(value, options.format);

        // senão formata data/numero
        return culture.format(value, options.format);
      };
    }

    column.filterInfo = createFilterInfo(column, options);

    this._columns.value.push(column);

    return this;
  }

  /**
   * Adiciona uma coluna que testa o campo (que deve ser boolean) e exibe um icone de Check ou X (ex: fgAtivo)
   * @param header Texto a exibir no botão
   * @param field Nome do campo do objeto T
   * @param options Opções de renderização (align, width)
   */
  boolean(
    header: string,
    field: string,
    width?: number,
    options?: GridViewItemOptions<T>
  ): GridViewOptions<T> {
    const column: DataTableColumn = {
      key: field,
      title: header,
      align: options?.align || "center",
      width: getWidth(width, 2),
      visible: _.isEmpty(options?.visible) ? true : !!options?.visible,
      sorter: (row1, row2) => customSort(field, row1, row2),
      render(row: any) {
        const result = objectPath.get(row, field);
        return h(Icon, {
          name:
            result === true
              ? Checkmark
              : result === false
              ? Close
              : EllipseOutline,
        });
      },
    };

    column.filterInfo = createFilterBoolean();

    this._columns.value.push(column);

    return this;
  }

  /**
   * Renderiza um componente ou um texto ou componente no lugar da coluna. Utilizado para condicionais ou colunas avançadas
   * @param header Titulo na header
   * @param width Largura da coluna (em rem)
   * @param fn Função de renderização. Retorna um componente ou um texto
   * @param options Opções de renderização (align, disabled, sort)
   */
  render(
    header: string,
    width: number | null = null,
    fn: (row: T, index?: number) => string | any,
    options?: GridViewItemOptions<T>
  ) {
    const column = {
      title: header,
      width: getWidth(width, undefined),
      visible: _.isEmpty(options?.visible) ? true : !!options?.visible,
      align: options?.align,
      disabled: options?.disabled,
      render: fn,
    };

    if (options?.sort) {
      column.sorter = (row1, row2) => customSort(options.sort, row1, row2);
    }

    this._columns.value.push(column);

    return this;
  }

  createControl(
    control: any,
    bindName: string,
    { header, field, width, controlOptions, options }
  ) {
    const renderFn = (row: T) => {
      const defaultOptions = {
        [bindName]: objectPath.get(row, field),
        ["onUpdate:" + bindName]: (v) => objectPath.set(row, field, v),
        disabled: options?.disabled ? options?.disabled(row) : false,
      };
      Object.assign(defaultOptions, controlOptions);
      return h(control, defaultOptions);
    };

    return this.render(header, width, renderFn, options);
  }

  /**
   * Renderiza um componente do tipo InputBox e faz bind do value com a field informado.
   * @param header Titulo na header
   * @param field Nome do campo
   * @param width Largura da coluna (em rem)
   * @param inputOptions Opções de customização do objeto InputBox (suas propriedades/eventos)
   * @param options Opções de renderização (align, disabled, sort)
   */
  inputbox(
    header: string,
    field: string,
    width?: number,
    inputOptions?: any,
    options?: GridViewItemOptions<T>
  ) {
    return this.createControl(InputBox, "value", {
      header,
      field,
      width,
      controlOptions: inputOptions,
      options,
    });
  }

  /**
   * Renderiza um componente do tipo ComboBox e faz bind do value com a field informado. Para popular a combo, utilize comboOptions
   * @param header Titulo na header
   * @param field Nome do campo
   * @param width Largura da coluna (em rem)
   * @param comboOptions Opções para customizar o componente de ComboBox. Utilize este campo para popular a combo. { options: [...], valueKey: "idPessoa", textKey: "noPessoa" })
   * @param options Opções de renderização (align, disabled, sort)
   */
  combobox(
    header: string,
    field: string,
    width?: number,
    comboOptions?: any,
    options?: GridViewItemOptions<T>
  ) {
    return this.createControl(ComboBox, "value", {
      header,
      field,
      width,
      controlOptions: comboOptions,
      options,
    });
  }

  /**
   * Renderiza um componente do tipo NumberBox e faz bind do value com a field informado.
   * @param header Titulo na header
   * @param field Nome do campo
   * @param width Largura da coluna (em rem)
   * @param numberOptions Opções para customizar o componente de NumberBox.
   * @param options Opções de renderização (align, disabled, sort)
   */
  numberbox(
    header: string,
    field: string,
    width?: number,
    numberOptions?: any,
    options?: GridViewItemOptions<T>
  ) {
    return this.createControl(NumberBox, "value", {
      header,
      field,
      width,
      controlOptions: numberOptions,
      options,
    });
  }

  /**
   * Renderiza um componente do tipo CheckBox e faz bind do checked (boolean) com a field informado.
   * @param header Titulo na header
   * @param field Nome do campo
   * @param width Largura da coluna (em rem)
   * @param checkboxOptions Opções para customizar o componente de CheckBox.
   * @param options Opções de renderização (align, disabled, sort)
   */
  checkbox(
    header: string,
    field: string,
    width?: number,
    checkboxOptions?: any,
    options?: GridViewItemOptions<T>
  ) {
    return this.createControl(CheckBox, "checked", {
      header,
      field,
      width,
      controlOptions: checkboxOptions,
      options,
    });
  }

  /**
   * Renderiza um componente do tipo DateBox e faz bind do date (Date) com a field informado.
   * @param header Titulo na header
   * @param field Nome do campo
   * @param width Largura da coluna (em rem)
   * @param dateboxOptions Opções para customizar o componente de DateBox.
   * @param options Opções de renderização (align, disabled, sort)
   */
  datebox(
    header: string,
    field: string,
    width?: number,
    dateboxOptions?: any,
    options?: GridViewItemOptions<T>
  ) {
    return this.createControl(DateBox, "date", {
      header,
      field,
      width,
      controlOptions: dateboxOptions,
      options,
    });
  }

  //
  createOptions() {
    const self = this;
    const opts = reactive({
      profileID: computed(() => this._profileID),
      scrollX: computed((x) => {
        const minWidth =
          document.querySelector(".main-content")?.offsetWidth || 960;
        const width = opts.visibleColumns
          .map((c) => c.width)
          .filter((v) => parseInt(v) > 0)
          .reduce((a, b) => a + b, 0);
        //const result = width > minWidth ? width : undefined;
        return width;
      }), // mantem undefined até chamar scrollX() na definição
      // retorna apenas as colunas que devem aparecer na grid
      visibleColumns: computed(() => {
        return this._columns.value.filter(
          (c) => c.visible === undefined || c.visible
        );
      }),
      // indica se esta utilizando profile
      useProfile: computed(() => !!this._profileID),
      // indica se a tela de filtro está aberta
      showFilter: ref(false),
      // retorna todas as colunas, mesmo as que não devem aparecer na grid
      columns: this._columns,
      // retorna o componente correto conforme filtro selecionado
      filterComponent: (index: number, column: any) => {
        const info = column.filterInfo;
        const control = filterControls[info.component];

        if (!control) alert("Controle não encontrado: " + info.component);

        const bindName = control[0];

        const componentOptions = {
          [bindName]: info.value[index],
          ["onUpdate:" + bindName]: (v) => (info.value[index] = v),
          valueKey: "value",
          textKey: "text",
        };

        if (_.compare(info.component, "ComboBox") === 0) {
          if (info.options.length === 0) {
            componentOptions.options = computed(() => {
              const allValues = new Set(
                this._data.value.map((x) => objectPath.get(x, column.key))
              );
              const list = [...allValues].map((x) => ({ value: x, text: x }));
              return list;
            });
            componentOptions.multiple = true;
          } else {
            componentOptions.options = info.options;
          }
        }

        // só exibe 2 componentes em caso de between
        if (index == 1 && info.op !== "between") {
          return null;
        }

        return h(control[1](), componentOptions);
      },
      filterClear: (column) => {
        column.filterInfo.op = null;
        column.filterInfo.value = [null];
      },
      filterClearAll: () => {
        this._columns.value.forEach((x) => {
          if (x.filterInfo) {
            x.filterInfo.op = null;
            x.filterInfo.value = [null];
          }
        });
      },
      filterLabel: computed(() => this.getFilterLabel()),
      filters: computed(() => this.getFilters()),
      totalCount: computed(() => this._data.value.length),
      data: computed(() => this.applyFilter()),
      loading: ref(false),
      exportToExcel: () => {
        exportToExcel(this.getFilterLabel(), this.getExcelData());
        notification.success("Tabela copiada para o clipboard com sucesso");
      },
      pagination: {
        pageSize: 10,
        pageSizes: [
          { value: 10, label: "10" },
          { value: 25, label: "25" },
          { value: 50, label: "50" },
          { value: 100, label: "100" },
          { value: 500, label: "500" },
        ],
        showSizePicker: true,
        onChange: (page: number) => {
          opts.pagination.page = page;
        },
        onUpdatePageSize: (pageSize: number) => {
          opts.pagination.pageSize = pageSize;
          opts.pagination.page = 1;
        },
      },
      profileInfo: computed(() =>
        getProfileObject(
          this._columns.value,
          opts,
          this._sortColumnKey?.value,
          this._sortOrder?.value
        )
      ),
      selectedItems: this._selectedItems,
      rowKey: (row: any) => objectPath.get(row, this._key),
      handleCheck(rowKeys: DataTableRowKey[]) {
        self._selectedItems.value = rowKeys;
      },
      handleSort(options: DataTableSortState | DataTableSortState[] | null) {
        if (self._sortColumnKey.value) {
          const col = self._columns.value.find(
            (x) => x.key == self._sortColumnKey.value
          );
          if (col) col.sortOrder = false;
        }
        self._sortColumnKey.value = options?.columnKey || null;
        self._sortOrder.value = options?.order || null;

        if (self._sortColumnKey.value) {
          const col = self._columns.value.find(
            (x) => x.key == self._sortColumnKey.value
          );
          if (col) col.sortOrder = self._sortOrder.value;
        }
      },
    });

    // monitora alterações no profile
    watch(
      () => opts.profileInfo,
      (v) => {
        if (this._profileID) {
          profile.set(this._profileID, JSON.stringify(v)); // async task
        }
      }
    );

    return opts;
  }
}

function getWidth(width?: number, defaultValue?: number) {
  if (width === null || width === undefined) return defaultValue * REM;
  return width * REM;
}

function customSort(field: string, row1: any, row2: any) {
  const value1 = objectPath.get(row1, field);
  const value2 = objectPath.get(row2, field);

  const r = _.compare(value1, value2);

  return r;
}

function createFilterInfo(column: any, options: any): FilterInfo {
  let component = "InputBox";
  let format = (value: any) => value;
  let ops = filterOps.map((x) => ({
    label: x[0],
    value: x[1],
  }));

  if (options?.format) {
    format = (value) => culture.format(value, options.format);
  }

  if (options?.format?.startsWith("c")) {
    component = "MoneyBox";
  } else if (options?.format?.startsWith("n")) {
    component = "NumberBox";
    format = (value) => '"' + culture.format(value, "yyyy-MM-dd") + '"';
  } else if (options?.format?.startsWith("d")) {
    component = "DateBox";
    format = (value) => '"' + culture.format(value, "yyyy-MM-dd") + '"';
  }

  if (options?.filter) {
    component = options?.filter;
  }

  if (_.compare(component, "ComboBox") === 0) {
    ops = [];
  }

  return {
    component,
    options: [],
    format,
    ops,
    op: null,
    value: [],
  };
}

function createFilterBoolean(): FilterInfo {
  return {
    component: "ComboBox",
    options: [
      { value: true, text: "Sim" },
      { value: false, text: "Não" },
    ],
    format: (value) => (value ? "Sim" : "Não"),
    ops: [],
    op: null,
    value: [],
  };
}

function getProfileObject(columns, opts, sortColumnKey, sortOrder) {
  return {
    columns: columns
      .filter((x) => x.visible !== undefined && x.filterInfo)
      .map((x) => ({
        field: x.key,
        visible: x.visible,
        op: x.filterInfo.op,
        value: x.filterInfo.value,
      })),
    pageSize: opts.pagination.pageSize,
    sortColumnKey,
    sortOrder,
  };
}

// carrega as colunas na ordem do profile
function sortColumns(columns: [], sorted: []) {
  const result: any[] = [];
  let offset = 0;

  for (let i = 0; i < columns.length; i++) {
    const col = columns[i];
    const idx = sorted.findIndex((x) => x.field == col.key);

    if (idx == -1) {
      result[i] = col;
      offset++;
    } else {
      col.visible = sorted[idx].visible;
      col.filterInfo.op = sorted[idx].op;
      col.filterInfo.value = sorted[idx].value;

      result[offset + idx] = col;
    }
  }

  return result;
}

// gera um uniqueID para a grid conforme a KEY e pagina
function getProfileID(key: string) {
  const route = useRoute();
  const str = route.path + "_" + key;

  return "grd_" + _.hashString(str);
}
