<script lang="ts" setup>
/**
 * Componente "n-select" from opção de indicar os dois campos (value/texto) da origem dos dados
 * <ComboBox :options="pessoas" value-key="cdPessoa" text-key="noPessoa"></ComboBox>
 */
import { NSelect } from "naive-ui";
import { computed, isRef } from "vue";

const props = withDefaults(
  defineProps<{
    options: Array<any>;
    valueKey?: string | number;
    textKey?: string | number;
    mini?: boolean;
  }>(),
  {
    options: () => [],
    valueKey: () => 0,
    textKey: () => 1,
    mini: () => false,
  }
);

const opts = computed(() => {
  const valueKey = props.valueKey || 0;
  const textKey = props.textKey || 1;

  if (!props.options) return [];

  const array = isRef(props.options)
    ? (props.options.value as [])
    : (props.options as []);

  return array.map((x) => ({
    value: x[valueKey],
    label: x[textKey],
  }));
});
</script>

<template>
  <n-select
    :options="opts"
    clearable
    :size="mini ? 'small' : 'medium'"
    :class="{ mini: props.mini }">
    <slot></slot>
  </n-select>
</template>
