<script setup lang="ts">
import { AlbumsOutline } from "@vicons/ionicons5";
import { onMounted, ref } from "vue";
import markdownit from "markdown-it";
import { notification } from "../../../core/notification";
import { user } from "../../../core/auth";
import help from "../../../core/help";

const props = withDefaults(
  defineProps<{
    id: string;
    mode?: "dock" | "float";
  }>(),
  {
    mode: () => "dock",
  }
);

const md = markdownit();

const el = ref();
const showHelp = ref(false);
const showEditor = ref(false);
const canEditHelp = ref(false);
const content = ref("");

onMounted(async () => {
  const helpText = await help.get(props.id);
  content.value = helpText.content;

  canEditHelp.value = user.value.roles.indexOf("HELP") >= 0;
});

async function saveHelp() {
  if (!content.value)
    return notification.error("O campo de descrição é obrigatório");

  await help.set(props.id, content.value);

  notification.success("Texto de ajuda registrado com sucesso");
  showEditor.value = false;
}

function openHelpBox(e) {
  e.preventDefault();
  showHelp.value = !showHelp.value;
}
</script>

<template>
  <div ref="el" class="help">
    <div class="help-field">
      <slot />
      <div class="help-right">
        <div class="help-btn" title="Ajuda" @click="openHelpBox">
          {{ showHelp ? "&#x2715;" : "?" }}
        </div>
      </div>
    </div>
    <div class="help-box" v-show="showHelp">
      <main>
        <div v-html="md.render(content || '')" class="markdown-body"></div>
      </main>
      <a
        class="n-link"
        v-show="canEditHelp"
        href="javascript:;"
        @click="() => (showEditor = true)">
        <Icon :name="AlbumsOutline" class="n-mr-1" />Editar
      </a>
    </div>
    <Popup v-model="showEditor" title="Editor de conteúdo de ajuda" :width="65">
      <div class="help-editor">
        <div class="help-editor-panel">
          <div class="help-editor-header">
            <Btn color="primary" :mini="true" @click="saveHelp">Salvar</Btn>
          </div>
          <InputBox
            v-model:value="content"
            type="textarea"
            :rows="25"
            maxlength="8000"
            placeholder="Descrição do texto de ajuda (suporta Markdown)" />
        </div>
        <div class="help-preview">
          <div class="help-box">
            <main
              v-html="md.render(content || '')"
              class="markdown-body"></main>
          </div>
          <a
            href="https://www.markdownguide.org/basic-syntax/"
            target="_blank"
            class="n-link">
            Guia básico de markdown
          </a>
        </div>
      </div>
    </Popup>
  </div>
</template>
