import api from "./api";

const DELAY = 2000;

let _promise = null;
let _data: Map = null;
const _delay = new Map();

async function getProfile(key) {
  if (!key) return null;

  if (_promise === null) {
    _promise = api("GET", "/nbox/profile", {}, null, true);
  }

  return _promise.then((data) => {
    if (_data === null) {
      _data = new Map(Object.entries(data));
    }
    return _data.get(key) || null;
  });
}

/**
 * Adiciona um key com um delay de chamada
 */
async function setProfile(key, content) {
  if (!key) return;

  if (_delay.has(key)) {
    const timer = _delay.get(key);
    clearTimeout(timer);
    _delay.delete(key);
  }

  const body = { content };
  const orig = await getProfile(key);

  if (orig !== content) {
    _data.set(key, content);

    const timer = setTimeout(() => {
      api("POST", `/nbox/profile/${key}`, {}, body, true);
    }, DELAY);

    _delay.set(key, timer);
  }
}

export default { get: getProfile, set: setProfile };
