export function exportToExcel(filter: string, data: []) {
  const rows = [];

  if (data.length === 0) return false;

  // escreve a header
  const firstRow = data[0];
  const header = [];
  for (let key in firstRow) {
    header.push(key);
  }

  for (let i = 0; i < data.length; i++) {
    const row = data[i];
    const columns = [];

    for (let key in row) {
      const value = row[key];
      columns.push(value);
    }

    rows.push(columns.join("\t"));
  }

  const output = header.join("\t") + "\n" + rows.join("\n");

  navigator.clipboard.writeText(output);
}
