<template>
  <div ref="el" class="draggable-container" :style="style">
    <div class="draggable-header" @mousedown="dragMouseDown">
      <slot name="header"></slot>
    </div>
    <slot name="main"></slot>
    <slot name="footer"></slot>
    <div class="draggable-resize" @mousedown="resizeMouseDown">◢</div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted } from "vue";
import profile from "../../../core/profile";

const style = reactive({
  top: undefined,
  left: undefined,
  height: undefined,
  widht: undefined,
});

const props = defineProps<{
  profileKey?: string;
}>();

const positions = reactive({
  clientX: undefined,
  clientY: undefined,
  movementX: 0,
  movementY: 0,
});

const resize = reactive({
  clientX: undefined,
  clientY: undefined,
  movementX: 0,
  movementY: 0,
});

const el = ref();

onMounted(async () => {
  if (!props.profileKey) return;
  const profileValue = await profile.get(props.profileKey);
  if (profileValue) {
    let { top, left, height, width } = JSON.parse(profileValue);

    // garante intervalos visiveis por mudanças de tela
    const wh = window.innerHeight;
    const ww = window.innerWidth;

    if (top < 0 || top > wh) top = 0;
    if (left < 0 || left > ww) left = 0;
    if (height < 0 || height > wh) height = wh - 30;
    if (width < 0 || width > ww) width = ww - 30;

    if (top + height > wh) top = wh - height;
    if (left + width > ww) left = ww - width;

    style.top = top + "px";
    style.left = left + "px";
    style.width = width + "px";
    style.height = height + "px";
  }
});

function dragMouseDown(e) {
  e.preventDefault();
  positions.clientX = e.clientX;
  positions.clientY = e.clientY;

  document.addEventListener("mousemove", elementDrag, false);
  document.addEventListener("mouseup", closeDragElement, false);
}

function elementDrag(e) {
  e.preventDefault();
  positions.movementX = positions.clientX - e.clientX;
  positions.movementY = positions.clientY - e.clientY;
  positions.clientX = e.clientX;
  positions.clientY = e.clientY;
  // set the element's new position:
  style.top = el.value.offsetTop - positions.movementY + "px";
  style.left = el.value.offsetLeft - positions.movementX + "px";

  updateProfile();
}

function closeDragElement() {
  document.removeEventListener("mousemove", elementDrag);
  document.removeEventListener("mouseup", closeDragElement);
}

function resizeMouseDown(e) {
  e.preventDefault();
  resize.clientX = e.clientX;
  resize.clientY = e.clientY;

  style.top = el.value.offsetTop + "px";
  style.left = el.value.offsetLeft + "px";

  updateProfile();

  document.addEventListener("mousemove", elementResize, false);
  document.addEventListener("mouseup", closeResizeElement, false);
}

function elementResize(e) {
  e.preventDefault();
  resize.movementX = resize.clientX - e.clientX;
  resize.movementY = resize.clientY - e.clientY;
  resize.clientX = e.clientX;
  resize.clientY = e.clientY;
  // set the element's new size:
  style.width = el.value.offsetWidth - resize.movementX + "px";
  style.height = el.value.offsetHeight - resize.movementY + "px";

  updateProfile();
}

function closeResizeElement() {
  document.removeEventListener("mousemove", elementResize);
  document.removeEventListener("mouseup", closeResizeElement);
}

function updateProfile() {
  if (!props.profileKey) return;

  const value = JSON.stringify({
    top: el.value.offsetTop,
    left: el.value.offsetLeft,
    height: el.value.offsetHeight,
    width: el.value.offsetWidth,
  });

  profile.set(props.profileKey, value);
}
</script>
