/**
 * Remove todos os caracteres que não são numericos da string. Retorna "" em caso vazio
 */
export function remove(value: string | null) {
  if (value === null) return null;

  const ret = [];

  for (let i = 0; i < value.length; i++) {
    if (!isNaN(parseInt(value.charAt(i)))) ret.push(value.charAt(i));
  }

  const result = ret.join("");

  return result === "" ? null : result;
}

/**
 * Aplica uma mascara simples sobre a string. Caso a string não seja compativel com
 * a mascara, retorna null
 */
export function apply(value: string | null, fmt: string) {
  // variavel limpa ou so espaços, retorna null
  if (value === null || value.trim().length === 0) return null;

  let unmasked = remove(value);

  if (unmasked === null) return null;

  const left = fmt.charAt(0).toUpperCase() == "R" ? false : true;

  if (fmt.charAt(0).toUpperCase() == "L" || fmt.charAt(0).toUpperCase() == "R")
    fmt = fmt.substr(1);

  if (unmasked.length !== remove(fmt)?.length) return null;

  if (unmasked.length < fmt.length) {
    for (let i = unmasked.length; i < fmt.length; i++) {
      if (left) unmasked += "~";
      else unmasked = "~" + unmasked;
    }
  }

  let ret = "";

  if (left) {
    let j = 0;

    for (let i = 0; i < fmt.length; i++) {
      if (fmt.charAt(i) == "0" || fmt.charAt(i) == "9") {
        if (unmasked.charAt(j) == "~") {
          ret += fmt.charAt(i) == "0" ? "0" : " ";
        } else {
          ret += unmasked.charAt(j);
        }
        j++;
      } else {
        ret += fmt.charAt(i);
      }
    }
  } else {
    let j = unmasked.length - 1;

    for (let i = fmt.length - 1; i >= 0; i--) {
      if (fmt.charAt(i) == "0" || fmt.charAt(i) == "9") {
        if (unmasked.charAt(j) == "~") {
          ret = (fmt.charAt(i) == "0" ? "0" : " ") + ret;
        } else {
          ret = unmasked.charAt(j) + ret;
        }
        j--;
      } else {
        ret = fmt.charAt(i) + ret;
      }
    }
  }

  return ret;
}
