<template>
  <div @dragover.prevent ref="el">
    <div
      v-for="(item, index) in props.modelValue"
      :key="item[props.itemKey]"
      :draggable="draggable"
      :class="props.itemClass"
      @dragstart="(e) => handleDragStart(index)"
      @drop="(e) => handleDrop(index)">
      <slot :item="item" />
    </div>
  </div>
</template>

<script setup lang="ts">
import { onMounted, ref, type Ref } from "vue";

const props = withDefaults(
  defineProps<{
    modelValue: Ref<[]>;
    itemKey: string;
    itemClass?: string;
    handle?: string;
  }>(),
  {
    itemClass: () => "",
    handle: () => ".handle",
  }
);

const emit = defineEmits<{
  (e: "update:modelValue", value: string | null): void;
}>();

const el = ref(null);
const draggable = ref(false);
let dragIndex = -1;

onMounted(() => {
  const handles = el.value.querySelectorAll(props.handle);

  for (let i = 0; i < handles.length; i++) {
    const handle = handles[i];
    handle.addEventListener("mousedown", () => (draggable.value = true));
    handle.addEventListener("mouseup", () => (draggable.value = false));
  }
});

function handleDragStart(index) {
  dragIndex = index;
}

function handleDrop(index) {
  draggable.value = false;
  const result = sortArray(props.modelValue, dragIndex, index);
  emit("update:modelValue", result);
}

function sortArray(array: [], fromIndex: number, toIndex: number) {
  if (fromIndex === toIndex) return array;

  const result = [...array];

  result.splice(fromIndex, 1);

  result.splice(toIndex, 0, array[fromIndex]);

  return result;
}
</script>
