<script setup lang="ts">
import { CloseOutline } from "@vicons/ionicons5";
import DraggableDiv from "./DraggableDiv.vue";
import { useRoute } from "vue-router";
import * as _ from "../../../core/underscore";

const props = defineProps<{
  modelValue: boolean;
  title?: string;
  width?: number;
  profileKey?: string;
}>();

const uniqueID =
  props.profileKey === undefined ? getUniqueID(props.title) : props.profileKey;

const emit = defineEmits<{
  (e: "update:modelValue", args: any): void;
}>();

function close() {
  emit("update:modelValue", false);
}

// gera um uniqueID para a grid conforme a titulo da popup e url da pagina
function getUniqueID(key: string) {
  const route = useRoute();
  const str = route.path + "_" + key;
  return "popup_" + _.hashString(str);
}
</script>

<template>
  <div class="popup-overlay" :class="{ open: modelValue }">
    <DraggableDiv
      :profileKey="uniqueID"
      class="popup"
      :style="{ width: +width > 0 ? width + 'rem' : 'auto' }">
      <template #header>
        <header class="popup-header">
          {{ title }}
          <CloseOutline @click="close()" class="popup-icon" title="Fechar" />
        </header>
      </template>
      <template #main>
        <main class="popup-main">
          <slot></slot>
        </main>
      </template>
    </DraggableDiv>
  </div>
</template>
