import { createDiscreteApi, darkTheme } from "naive-ui";
import { h } from "vue";

const { dialog, message } = createDiscreteApi(["dialog", "message"], {
  configProviderProps: {
    theme: darkTheme,
  },
});

export const notification = {
  success: function (text: string) {
    return new Promise(function (resolve) {
      message.success(text, {
        onAfterLeave: () => resolve(),
      });
    });
  },

  error: function (text: string) {
    return new Promise(function (resolve) {
      message.error(text, {
        onAfterLeave: () => resolve(),
      });
    });
  },

  alert: function (
    text: string,
    options?: { details: string[]; title: string }
  ) {
    return new Promise(function (resolve) {
      const content = () => {
        return h("div", null, [
          h("div", {
            style: { fontSize: "18px", marginTop: "1rem" },
            innerHTML: text.replace(/\n/g, "<br/>"),
          }),
          options?.details
            ? h(
                "div",
                {
                  style: {
                    marginTop: "1rem",
                    color: "deepskyblue",
                    fontSize: "12px",
                  },
                },
                options?.details?.map((item) => h("div", { innerHTML: item }))
              )
            : null,
        ]);
      };

      dialog.error({
        title: options?.title || "Erro ao processar requisição",
        content,
        positiveText: "Ok",
        onPositiveClick: () => resolve(),
        onClose: () => resolve(),
        onMaskClick: () => resolve(),
      });
    });
  },

  confirm: function (message = "Confirma excluir os registros selecionados?") {
    return new Promise<boolean>(function (resolve) {
      dialog.error({
        title: "Confirmar operação",
        content: message,
        positiveText: "Ok",
        negativeText: "Cancelar",
        onPositiveClick: () => {
          resolve(true);
        },
        onNegativeClick: () => {
          resolve(false);
        },
      });
    });
  },
};
