//
// Culture.js - javascript culture extensions - Date/Number parser/formatter
//

// preciso adicionar parserDate com local/utc e .ffff -timezoneOffset
import * as _ from "./underscore";

const languages = {
  enus: {
    lang: "en-US",
    date: {
      patterns: ["M/d", "M/d/y", "h:m", "h:m t"],
      // http://msdn.microsoft.com/en-us/library/az4se3k1(v=vs.110).aspx
      format: {
        d: "MM/dd/yyyy",
        D: "dddd, MMMM dd, yyyy",
        f: "dddd, MMMM dd, yyyy - hh:mm tt",
        F: "dddd, MMMM dd, yyyy - hh:mm:ss tt",
        g: "MM/dd/yyyy hh:mm tt",
        G: "MM/dd/yyyy hh:mm:ss tt",
        m: "MMMM dd",
        s: "yyyy-MM-ddTHH:mm:ss",
        t: "hh:mm tt",
        T: "hh:mm:ss tt",
        y: "MMMM, yyyy",
        u: "UTC:yyyy-MM-ddTHH:mm:ssZ",
      },
      ampm: ["AM", "PM"],
    },
    number: {
      group: [",", 3],
      decimal: [".", 2],
      currency: ["($ |)", "$ |"],
    },
    calendar: {
      dayNames: [
        "Sunday",
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
      ],
      shortDayNames: ["Sun", "Mon", "Tue", "Wed", "Thu", "Fri", "Sat"],
      monthNames: [
        "January",
        "February",
        "March",
        "April",
        "May",
        "June",
        "July",
        "August",
        "September",
        "October",
        "November",
        "December",
      ],
      shortMonthNames: [
        "Jan",
        "Feb",
        "Mar",
        "Apr",
        "May",
        "Jun",
        "Jul",
        "Aug",
        "Sep",
        "Oct",
        "Nov",
        "Dec",
      ],
      duration: [
        "now",
        ["second", "seconds"],
        ["minute", "minutes"],
        ["hour", "hours"],
        ["day", "days"],
        ["month", "months"],
        ["year", "years"],
      ],
    },
  },
  ptbr: {
    lang: "pt-BR",
    date: {
      patterns: [
        "d",
        "d/M",
        "d/M/y",
        "d/M/y h:m",
        "d/M/y h:m:s",
        "h:m",
        "h:m:s",
      ],
      format: {
        d: "dd/MM/yyyy",
        D: 'dddd, dd "de" MMMM "de" yyyy',
        f: 'dddd, dd "de" MMMM "de" yyyy - HH:mm',
        F: 'dddd, dd "de" MMMM "de" yyyy - HH:mm:ss',
        g: "dd/MM/yyyy HH:mm",
        G: "dd/MM/yyyy HH:mm:ss",
        m: "dd de MMMM",
        s: "yyyy-MM-ddTHH:mm:ss",
        t: "HH:mm",
        T: "HH:mm:ss",
        y: "MMMM/yyyy",
        u: "UTC:yyyy-MM-ddTHH:mm:ssZ",
      },
      ampm: null,
    },
    number: {
      group: [".", 3],
      decimal: [",", 2],
      currency: ["-R$ |", "R$ |"],
    },
    calendar: {
      dayNames: [
        "Domingo",
        "Segunda-Feira",
        "Terça-Feira",
        "Quarta-Feira",
        "Quinta-Feira",
        "Sexta-Feira",
        "Sábado",
      ],
      shortDayNames: ["Dom", "Seg", "Ter", "Qua", "Qui", "Sex", "Sab"],
      monthNames: [
        "Janeiro",
        "Fevereiro",
        "Marco",
        "Abril",
        "Maio",
        "Junho",
        "Julho",
        "Agosto",
        "Setembro",
        "Outubro",
        "Novembro",
        "Dezembro",
      ],
      shortMonthNames: [
        "Jan",
        "Fev",
        "Mar",
        "Abr",
        "Mai",
        "Jun",
        "Jul",
        "Ago",
        "Set",
        "Out",
        "Nov",
        "Dez",
      ],
      duration: [
        "agora",
        ["um segundo", "{0} segundos"],
        ["um minuto", "{0} minutos"],
        ["uma hora", "{0} horas"],
        ["um dia", "dias"],
        ["um mes", "{0} meses"],
        ["um ano", "{0} anos"],
      ],
    },
  },
};

const cache = {};

// cria uma nova culture
export const useCulture = (lang?: string) => {
  const culture = languages[(lang || "ptbr").toLowerCase().replace("-", "")];

  // initial parser patterns
  const patterns = ["y-M-d", "y-M-dTh:m:s(\\.f)?(o)?", "y-M-dTh:m:s(\\.f)?Z"];

  for (let p = 0; p < culture.date.patterns.length; p++) {
    patterns.push(culture.date.patterns[p]);
  }

  if (!culture) console.error("Language not defined: " + lang);

  // Parse any string to Date object using current culture info. Returns null if not a valid date
  const parseDate = function (str) {
    if (str === null || _.isDate(str) || typeof str === "undefined")
      return str || null;

    let result = null;

    //console.log(patterns);

    for (let i = 0; i < patterns.length; i++) {
      result = keyMatch(str.toString(), patterns[i]);
      if (result !== null) break;
    }

    if (result === null) return null;

    const now = new Date();

    result.d = parseInt(result.d) || now.getDate();
    result.M = parseInt(result.M) || now.getMonth() + 1;
    result.y = parseInt(result.y) || now.getFullYear();
    result.h = parseInt(result.h) || 0;
    result.m = parseInt(result.m) || 0;
    result.s = parseInt(result.s) || 0;
    result.f = parseInt(result.f) || 0;

    if (
      culture.date.ampm &&
      new RegExp(culture.date.ampm[1], "i").test(result.t)
    )
      result.h += 12;
    if (result.M < 1 || result.M > 12) return null;
    if (result.y < 50) result.y += 2000;
    if (result.y > 50 && result.y < 99) result.y += 1900;
    if (result.h > 23) return null;
    if (result.m > 59) return null;
    if (result.s > 59) return null;
    if (result.d < 1 || result.d > getDaysInMonth(result.M - 1, result.y))
      return null;

    return result.utc
      ? new Date(
          Date.UTC(
            result.y,
            result.M - 1,
            result.d,
            result.h,
            result.m,
            result.s,
            result.f
          )
        )
      : new Date(
          result.y,
          result.M - 1,
          result.d,
          result.h,
          result.m,
          result.s,
          result.f
        );
  };

  // Parse any string to Number object using current culture info. Returns null if not a valid number
  const parseNumber = function (str) {
    if (str === null || _.isNumber(str) || typeof str === "undefined")
      return str || null;

    const group = new RegExp(culture.number.group[0].replace(".", "\\."), "g");
    const decimal = new RegExp(
      culture.number.decimal[0].replace(".", "\\."),
      "g"
    );

    const num = str.toString().replace(group, "").replace(decimal, ".");

    const val = parseFloat(num);

    if (isNaN(val)) return null;

    return val;
  };

  // Format String (like string.Format in C#), Number or Date according value object type
  const format = function (value, fmt) {
    const args = arguments;

    if (value === null || value === undefined) return "";

    if (_.isDate(value)) return dateFormat(value, fmt, false);

    if (_.isNumber(value)) {
      const mask = fmt || "n";
      const decimals = /^[nc](\d+)$/i.test(mask)
        ? mask.match(/^[nc](\d+)$/i)[1]
        : culture.number.decimal[1];

      if (/^c\d*$/i.test(mask)) {
        const str = numberFormat(
          Math.abs(value),
          decimals,
          culture.number.decimal[0],
          culture.number.group[0],
          culture.number.group[1]
        );
        const currency =
          value < 0 ? culture.number.currency[0] : culture.number.currency[1];
        return currency.replace("|", str);
      } else if (/^n\d*$/i.test(mask)) {
        return numberFormat(
          value,
          decimals,
          culture.number.decimal[0],
          culture.number.group[0],
          culture.number.group[1]
        );
      }
    }

    if (_.isString(value)) {
      return value.replace(/{(\d+(:[^}]*)?)}/, function (k, m) {
        const mask = m.replace(/^\d+:?/, "");
        const index = parseInt(m.match(/^\d+/)[0]);
        const arg = args[index + 1];
        return mask ? format(arg, mask) : arg;
      });
    }

    return value.toString();
  };

  // Add an interval into a Date returning a new Date
  const dateAdd = function (datepart, number, date) {
    const d = new Date(date.getTime());
    let m = "";

    switch (datepart) {
      case "y":
        m = "FullYear";
        break;
      case "M":
        d.setDate(1);
        d.setMonth(d.getMonth() + parseInt(number || 0));
        d.setDate(
          Math.min(
            date.getDate(),
            getDaysInMonth(d.getMonth(), d.getFullYear())
          )
        );
        return d;
      case "d":
        m = "Date";
        break;
      case "h":
        m = "Hour";
        break;
      case "m":
        m = "Minute";
        break;
      case "s":
        m = "Second";
        break;
      case "f":
        m = "Milliseconds";
        break;
      default:
        throw "i18n.dateAdd : part no recognize. Use y, M, d, h, m, s or f";
    }

    d["set" + m](d["get" + m]() + parseInt(number || 0));

    return d;
  };

  // Get difference between two dates. Must specify datepart
  const dateDiff = function (datepart, start, end) {
    if (end == null || start == null) return null;

    const t2 = end.getTime();
    const t1 = start.getTime();
    let ms = 0;

    switch (datepart) {
      case "y":
      case "M": {
        let months =
          end.getMonth() -
          start.getMonth() +
          12 * (end.getFullYear() - start.getFullYear());
        if (end.getDate() < start.getDate()) {
          months--;
        }
        return datepart === "M" ? months : Math.ceil(months / 12);
      }
      case "d":
        ms = 24 * 60 * 60 * 1000;
        break;
      case "h":
        ms = 60 * 60 * 1000;
        break;
      case "m":
        ms = 60 * 1000;
        break;
      case "s":
        ms = 1000;
        break;
      case "f":
        ms = 1;
        break;
      default:
        throw "i18n.dateDiff: part no recognize. Use y, M, d, h, m, s or f";
    }

    return parseInt((t2 - t1) / ms);
  };

  // Returns a string with time ago from a Date
  const timeAgo = function (date) {
    const diff = new Date().getTime() - date.getTime();

    const r = {
      y: parseInt(diff / (365 * 24 * 60 * 60 * 1000)),
      M: parseInt(diff / (30 * 24 * 60 * 60 * 1000)),
      d: parseInt(diff / (24 * 60 * 60 * 1000)),
      h: parseInt(diff / (60 * 60 * 1000)),
      m: parseInt(diff / (60 * 1000)),
      s: parseInt(diff / 1000),
    };

    return r.y >= 1
      ? format(culture.calendar.duration[6][r.y === 1 ? 0 : 1], r.y)
      : r.M >= 1
      ? format(culture.calendar.duration[5][r.M === 1 ? 0 : 1], r.M)
      : r.d >= 1
      ? format(culture.calendar.duration[4][r.d === 1 ? 0 : 1], r.d)
      : r.h >= 1
      ? format(culture.calendar.duration[3][r.h === 1 ? 0 : 1], r.h)
      : r.m >= 1
      ? format(culture.calendar.duration[2][r.m === 1 ? 0 : 1], r.m)
      : r.s >= 1
      ? format(culture.calendar.duration[1][r.s === 1 ? 0 : 1], r.s)
      : culture.calendar.duration[1][1];
  };

  const keyMatch = function (str, pattern) {
    let expr = { keys: {}, re: "" };
    const result = {};

    if (cache[pattern]) {
      expr = cache[pattern];
    } else {
      let re =
        "^" +
        pattern
          .replace("d", "(?<d>\\d{1,2})")
          .replace("y", "(?<y>\\d{2,4})")
          .replace("M", "(?<M>\\d{1,2})")
          .replace("h", "(?<h>\\d{1,2})")
          .replace("m", "(?<m>\\d{1,2})")
          .replace("s", "(?<s>\\d{1,2})")
          .replace("f", "(?<f>\\d{1,12})")
          .replace("o", "(?<o>[+-]?\\d{1,2}:?\\d{1,2})") +
        "$";

      if (culture.date.ampm)
        re = re.replace(
          "t",
          "(?<t>(" + culture.date.ampm[0] + ")|(" + culture.date.ampm[1] + "))"
        );

      const keysIndex = re.match(/(?!\(\?\<)(\w+)(?=\>)/g);
      if (!keysIndex) {
        // no keys, do a regular match
        return str.match(re);
      } else {
        for (let i = 0; i < keysIndex.length; i++) {
          expr.keys[i + 1] = keysIndex[i];
        }
      }
      expr.re = RegExp(re.replace(/\?\<\w+\>/g, ""), "i");
      cache[re] = expr;
    }

    const match = str.match(expr.re);

    if (match) {
      const keys = Object.keys(expr.keys);
      for (i = 0; i < keys.length; i++) {
        result[expr.keys[keys[i]]] = match[i + 1];
      }
      result.utc = pattern.indexOf("Z") >= 0;
      return result;
    } else {
      return null;
    }
  };

  // http://blog.stevenlevithan.com/archives/date-time-format
  const dateFormat = (function () {
    const token =
      /d{1,4}|M{1,4}|yy(?:yy)?|f{1,3}|z{1,3}|([Hhmst])\1?|"[^"]*"|'[^']*'/g;
    const pad = function (val, len) {
      val = String(val);
      len = len || 2;
      while (val.length < len) val = "0" + val;
      return val;
    };

    // regexes and supporting functions are cached through closure
    return function (date, mask, utc) {
      // fixed mask 'ago' uses timeAgo function
      if (mask === "ago") return timeAgo(date);

      mask = String(
        culture.date.format[mask] || mask || culture.date.format["d"]
      );

      // allow setting the utc argument via the mask
      if (mask.slice(0, 4) == "UTC:") {
        mask = mask.slice(4);
        utc = true;
      }

      const ampm = culture.date.ampm || ["", ""];

      // http://msdn.microsoft.com/en-us/library/8kb3ddd4(v=vs.110).aspx
      const _ = utc ? "getUTC" : "get",
        d = date[_ + "Date"](),
        D = date[_ + "Day"](),
        M = date[_ + "Month"](),
        y = date[_ + "FullYear"](),
        H = date[_ + "Hours"](),
        m = date[_ + "Minutes"](),
        s = date[_ + "Seconds"](),
        f = date[_ + "Milliseconds"](),
        o = utc ? 0 : date.getTimezoneOffset(),
        flags = {
          d: d,
          dd: pad(d),
          ddd: culture.calendar.shortDayNames[D],
          dddd: culture.calendar.dayNames[D],
          M: M + 1,
          MM: pad(M + 1),
          MMM: culture.calendar.shortMonthNames[M],
          MMMM: culture.calendar.monthNames[M],
          yy: String(y).slice(2),
          yyyy: y,
          h: H % 12 || 12,
          hh: pad(H % 12 || 12),
          H: H,
          HH: pad(H),
          m: m,
          mm: pad(m),
          s: s,
          ss: pad(s),
          f: pad(f, 3).substring(0, 1),
          ff: pad(f, 3).substring(0, 2),
          fff: pad(f, 3),
          t: H < 12 ? ampm[0].substring(0, 1) : ampm[1].substring(0, 1),
          tt: H < 12 ? ampm[0] : ampm[1],
          z: (o > 0 ? "-" : "+") + Math.floor(Math.abs(o) / 60),
          zz:
            (o > 0 ? "-" : "+") +
            pad(
              Math.floor(Math.abs(o) / 60) * 100 + (Math.abs(o) % 60),
              4
            ).substring(0, 2),
          zzz:
            (o > 0 ? "-" : "+") +
            pad(Math.floor(Math.abs(o) / 60) * 100 + (Math.abs(o) % 60), 4),
        };

      return mask.replace(token, function ($0) {
        return $0 in flags ? flags[$0] : $0.slice(1, $0.length - 1);
      });
    };
  })();

  const numberFormat = function (n, c, d, t, g) {
    c = isNaN((c = Math.abs(c))) ? 2 : c;
    d = d === undefined ? "." : d;
    t = t === undefined ? "," : t;
    const s = n < 0 ? "-" : "";
    const i = parseInt((n = Math.abs(+n || 0).toFixed(c))) + "";
    let j;
    j = (j = i.length) > g ? j % g : 0;
    return (
      s +
      (j ? i.substr(0, j) + t : "") +
      i
        .substr(j)
        .replace(new RegExp("(\\d{" + g + "})(?=\\d)", "g"), "$1" + t) +
      (c
        ? d +
          Math.abs(n - i)
            .toFixed(c)
            .slice(2)
        : "")
    );
  };

  const isLeapYear = function (year) {
    return (year % 4 === 0 && year % 100 !== 0) || year % 400 === 0;
  };

  const getDaysInMonth = function (month, year) {
    return [
      31,
      isLeapYear(year) ? 29 : 28,
      31,
      30,
      31,
      30,
      31,
      31,
      30,
      31,
      30,
      31,
    ][month];
  };

  return {
    culture,
    format,
    parseDate,
    parseNumber,
    timeAgo,
    dateAdd,
    dateDiff,
  };

  // exposing methods
};
