<script lang="ts" setup>
import { inject } from "vue";

const props = withDefaults(
  defineProps<{
    label?: string;
    required?: boolean;
    field?: string;
    validate?: (value: any) => boolean | string;
    help?: string;
  }>(),
  {
    required: false,
  }
);

const errors = inject("errors", []);
const labelWidth = inject("labelWidth", 8);
</script>

<template>
  <div class="form-item">
    <label
      class="form-label"
      :class="'form-label-' + (labelWidth || 8)"
      v-if="label !== undefined">
      {{ label }}
    </label>
    <div class="form-slot">
      <slot></slot>
      <div class="form-validation">
        {{
          errors
            .filter((x) => x.field === field)
            .map((x) => x.message)
            .join("\n ")
        }}
      </div>
      <div v-if="help" class="form-help">{{ help }}</div>
    </div>
  </div>
</template>
