<script lang="ts" setup>
/**
 * Componente "n-input-number" com formato nacional de moeda
 */
import { NInputNumber } from "naive-ui";
import { useCulture } from "../../../core/culture";

const props = withDefaults(
  defineProps<{
    format?: string;
    mini?: boolean;
  }>(),
  {
    format: () => "n2",
    mini: () => false,
  }
);

const culture = useCulture();

function parseDecimal(input: string) {
  return culture.parseNumber(input);
}

function formatDecimal(value: number | null) {
  return culture.format(value, props.format);
}
</script>

<template>
  <n-input-number
    clearable
    :parse="parseDecimal"
    :format="formatDecimal"
    class="decimal-box"
    :class="{ mini: props.mini }"
    placeholder=""
    :show-button="false">
  </n-input-number>
</template>
