<script lang="ts" setup>
/**
 * Componente "n-input" que garante o "null" no "v-model:value" quando componente está vazio
 */
import { NInput } from "naive-ui";
import { onMounted, ref, watch } from "vue";

const props = withDefaults(
  defineProps<{
    value?: any;
    textTransform?: "uppercase" | "lowercase" | "none";
    mini?: boolean;
  }>(),
  {
    textTransform: () => "none",
    mini: () => false,
  }
);

const emit = defineEmits<{
  (e: "update:value", value: string | null): void;
}>();

const localValue = ref<string | null>(props.value);

onMounted(() => {
  //
  localValue.value = getValue(props.value);

  emit("update:value", localValue.value);
});

watch(() => props.value, onInput);

function onInput(newValue: string) {
  //
  localValue.value = getValue(newValue);

  emit("update:value", localValue.value);
}

// aplica trim() para null no blur
function onBlur() {
  if (localValue.value && localValue.value.toString().trim().length === 0) {
    localValue.value = null;
    emit("update:value", null);
  }
}

// retorna o valor formatado conforme o text-transform
function getValue(value: any) {
  if (value === null || value === undefined || value === "") {
    return null;
  }
  if (props.textTransform === "lowercase") {
    return value.toLocaleLowerCase();
  } else if (props.textTransform === "uppercase") {
    return value.toLocaleUpperCase();
  }
  return value;
}
</script>

<template>
  <n-input
    v-model:value="localValue"
    @input="onInput"
    @blur="onBlur"
    placeholder=""
    :maxlength="100"
    class="control"
    :class="{
      mini: props.mini === true,
      uppercase: textTransform === 'uppercase',
      lowercase: textTransform === 'lowercase',
    }"
    clearable />
</template>
