<script lang="ts" setup>
import { NTransfer } from "naive-ui";
import { computed } from "vue";

const props = defineProps<{
  options: Array<any>;
  valueKey: string;
  textKey: string | ((row: any) => string);
}>();

const opts = computed(() => {
  const valueKey = props.valueKey || 0;

  return props.options?.map((x) => ({
    value: x[valueKey],
    label:
      typeof props.textKey === "string" ? x[props.textKey] : props.textKey(x),
  }));
});
</script>

<template>
  <n-transfer
    ref="transfer"
    virtual-scroll
    :options="opts"
    source-filterable
    target-filterable
    source-title="Disponíveis"
    target-title="Selecionados"
    style="width: 100%" />
</template>
