import api from "./api";

let _promise = null;

function getHelpText(key): {
  content: string;
  userId: string;
  lastUpdate: Date;
} {
  if (_promise === null) {
    _promise = api("GET", "/nbox/help-text", {}, null, true);
  }

  return _promise.then((data) => {
    return (
      data[key] || {
        title: `id: [${key}]`,
        content: "Ajuda não encontrada",
        userId: "",
        lastUpdate: null,
      }
    );
  });
}

async function setHelpText(id, content) {
  const body = { content };

  return await api("POST", `/nbox/help-text/${id}`, {}, body, true);
}

export default { get: getHelpText, set: setHelpText };
